[ng\:cloak],
[ng-cloak],
[data-ng-cloak],
[x-ng-cloak],
.ng-cloak,
.x- ng-cloak {
  display: none !important; }

p,
h1,
h2,
h3,
h4,
h5 {
  font-family: 'Lato', sans-serif; }

.no-padding {
  padding: 0px; }

.head {
  background: url("./../images/abstract-mosaic-background-1.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  color: white;
  padding: 2em; }
  .head img {
    border-radius: 50%; }
    @media screen and (max-width: 767px) {
      .head img {
        max-height: 120px; } }
  .head p {
    font-size: 16px;
    text-align: center;
    padding: 6px 10px;
    border-radius: 3px;
    display: inline-block;
    background-color: #FAF46A;
    color: black;
    opacity: 70%;
    font-style: italic;
    line-height: 16px; }
  .head h1 {
    font-weight: 300;
    font-size: 1.4em;
    line-height: 1.4; }
  .head .navigation {
    margin-top: 15px; }
    .head .navigation a {
      font-size: 43.2px;
      line-height: 77.76px;
      letter-spacing: -2px;
      font-weight: bold;
      color: #ffffff; }

#about-me .intro h2 {
  line-height: 44.8px;
  font-weight: 300;
  margin: 32px 0;
  color: #334455; }
  #about-me .intro h2 .rotateWord {
    display: inline-block;
    text-align: left;
    text-indent: 4px;
    min-height: 34px; }
    #about-me .intro h2 .rotateWord span {
      position: absolute;
      font-weight: 800;
      letter-spacing: -1px; }
  #about-me .intro h2 .swap-animation.ng-leave {
    animation: rotateOutWord .5s linear forwards; }
  #about-me .intro h2 .swap-animation.ng-enter {
    animation: rotateInWord .5s linear forwards; }

#about-me .section-title {
  position: relative;
  margin-bottom: 1em;
  font-size: 16px;
  letter-spacing: 1px;
  font-weight: 700; }
  #about-me .section-title span {
    position: relative;
    display: inline-block;
    background: #fff;
    line-height: 19.92px;
    letter-spacing: 1px;
    color: #334455;
    padding-right: 20px;
    text-align: left; }
    #about-me .section-title span i {
      border-radius: 50%;
      border: 4px solid #F4F6F7;
      margin-right: 14px;
      color: #B7C6C9;
      line-height: 39px;
      text-align: center;
      width: 47px; }
  #about-me .section-title:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    margin-top: 3px;
    width: 100%;
    border-bottom: 4px solid #f4f6f7; }

#latest-tweets ul {
  padding-left: 0px; }
  @media screen and (min-width: 767px) {
    #latest-tweets ul {
      padding-left: 40px; } }
  #latest-tweets ul li {
    list-style: none;
    position: relative;
    margin: 20px 20px 3px 20px;
    padding-top: .1em;
    font-size: 14px;
    font-family: FontAwesome;
    font-weight: normal;
    text-align: left;
    font-style: normal;
    display: inline-block;
    text-decoration: inherit; }
    @media screen and (min-width: 767px) {
      #latest-tweets ul li {
        margin: 20px 0 0 0; } }
    #latest-tweets ul li .tweet {
      background-color: #E4F1F6;
      color: #6a7686;
      border-radius: 4px;
      padding: 20px; }
    #latest-tweets ul li:before {
      content: "\f099";
      position: absolute;
      top: -24px;
      left: 50%;
      font-size: 36px;
      color: #567;
      margin-left: -16px; }
    #latest-tweets ul li .timePosted {
      text-decoration: none;
      letter-spacing: 1px;
      font-size: 10px;
      line-height: 18px;
      color: #cccccc; }

.services {
  text-align: center; }
  .services p {
    font-size: 15px;
    line-height: 24.9px;
    margin-top: .4em;
    color: #96A5A8; }
  .services i {
    display: inline-block;
    width: 86px;
    line-height: 86px;
    font-size: 32px;
    color: #fff;
    border-radius: 50%;
    text-align: center; }
  .services .color1 {
