    .services .color1 i {
      background: #f8cb30; }
  .services .color2 {
    padding: 32px 0px; }
    .services .color2 i {
      background: #0092DA; }
  .services .color3 {
    padding: 32px 0px; }
    .services .color3 i {
      background: #39CCC5; }
  .services .color4 {
    padding: 32px 0px;
    padding: 32px 0px; }
    .services .color4 i {
      background: #E25E5B; }

.work-process img {
  height: 70px;
  margin: 30px auto 15px; }

.work-process .arrow {
  height: 20px;
  float: right;
  position: absolute;
  top: 20%;
  right: 0; }

.work-process p {
  font-size: 18px;
  font-weight: 400; }

.work-process h4 {
  margin-bottom: 32px; }

@keyframes rotateOutWord {
  0% {
    opacity: 1;
    transform: translateY(0px); }
  100% {
    opacity: 0;
    transform: translateY(30px); } }

@keyframes rotateInWord {
  0% {
    opacity: 0;
    transform: translateY(-30px); }
  100% {
    opacity: 1;
    transform: translateY(0px); } }
